import { html } from "lit-html";
import * as Components from "@webs/component-library";
import "./button.css";

/**
 * Primary UI component for user interaction
 */
export const Button = ({ text, extraClasses, href, openInNewTab }) => {
  return html`
    <Components.webs-button
      type=${text}
      class=${extraClasses}
      href=${href}
      openInNewTab=${openInNewTab}
    >
    </Components.webs-button>
  `;
};
